import React, { Component } from 'react';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText, Alert } from 'reactstrap';
import { CardGroup, Container, Form, FormGroup, Fade, Collapse, Label, ListGroup, ListGroupItem, ListGroupItemHeading, ListGroupItemText } from 'reactstrap'
import { Card, CardBody, CardHeader } from 'reactstrap'

const { PacmanLoader } = require('react-spinners')
const { calcAccountId } = require('gege-util')
const WebTorrent = require('gege-torrent')
const miner = require('gege-miner')
const REPORT_INTERVAL = 1000 * 60 * 5;
let DEBUG = false;
function prettyBytes(num) {
    var exponent, unit, neg = num < 0, units = ['B', 'kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
    if (neg) num = -num
    if (num < 1) return (neg ? '-' : '') + num + ' B'
    exponent = Math.min(Math.floor(Math.log(num) / Math.log(1000)), units.length - 1)
    num = Number((num / Math.pow(1000, exponent)).toFixed(2))
    unit = units[exponent]
    return (neg ? '-' : '') + num + ' ' + unit
}

class WebMiner extends Component {

    constructor(props) {
        super(props);
        this.state = { mining: false, bonus: 0.1, rsId: '', needSecret: false, totalPower: 0, downloadSpeed: 0, uploadSpeed: 0 }
        this.torrents = [];
        //
        let rtcConfig = {
            "iceServers": [
                { "urls": "stun:stun2.l.google.com:19302" },
                { "urls": "stun:stun3.l.google.com:19302" },
                {
                    "urls": "stun:global.stun.twilio.com:3478?transport=udp"
                }
            ]
        };
        this.client = new WebTorrent({
            tracker: {
                rtcConfig: rtcConfig,
            }
        })
    }

    componentDidMount() {
        this.secret = window.sessionStorage.getItem("userSec");
        if (!this.secret) {
            this.secret = window.localStorage.getItem("userSec");
        }
        if (this.secret && this.secret.length > 30) {
            let rsId = calcAccountId(this.secret);
            this.setState({ rsId })
        } else {
            this.setState({ needSecret: true })
        }
    }

    toggle() {
        if (!this.state.mining) {
            if (!this.secret || this.secret.length < 30) {
                this.setState({ msg: 'need BTB account secret(需要BTB账户密码)' })
                return;
            }
        }
        let mining = !this.state.mining;
        this.setState({ mining });
        if (mining) {
            //start
            window.localStorage.setItem('userSec', this.secret);
            miner.setSecret(this.secret);
            setTimeout(this.startMining.bind(this), 2000);
        } else {
            //stop
            this.stopMining()
        }
    }

    stopMining() {
        clearInterval(this.reportInterval);
    }

    calcMinerSpeed() {
        let up = 0, down = 0;
        for (var idx in this.torrents) {
            let tor = this.torrents[idx];
            up += tor.uploadSpeed;
            down += tor.downloadSpeed;
        }
        if (up > 0)
            up = prettyBytes(up)
        if (down > 0)
            down = prettyBytes(down)
        this.setState({ uploadSpeed: up, downloadSpeed: down })
    }

    onTorrent(torrent) {
        var info = 'Torrent info hash: ' + torrent.infoHash +
            'magnetURI=' + torrent.magnetURI +
            ' torrentFileBlobURL="' + torrent.torrentFileBlobURL + 'name=' + torrent.name;
        // console.log(info);
        //TODO check if exist or not
        this.torrents.push(torrent);
    }

    startMining() {
        let options = { client: 'web', spaceM: -1, freeSpace: -1 };
        miner.start(options, ((res) => {
            if (!res) {
                this.setState({ mining: false })
                return;
            }
            let resources = res.resources;
            if (!resources || resources.length == 0) {
                this.setState({ mining: false })
                return;
            }
            // console.log('onResources ' + resources);
            //
            for (var idx in resources) {
                let res = resources[idx];
                if (res.isSeed) {
                    this.client.add(res.hash, this.onTorrent.bind(this));
                } else {
                    // console.log('ignore not seed resource')
                }
            }
            //

        }).bind(this))
        //
        this.reportInterval = setInterval(this.doReport.bind(this), DEBUG ? 60 * 1000 : REPORT_INTERVAL);
        this.speedInterval = setInterval(this.calcMinerSpeed.bind(this), 1000)
    }

    doReport() {
        let torrentsInfo = [];
        for (var idx in this.torrents) {
            let tor = this.torrents[idx];
            let info = { hash: tor.infoHash, progress: tor.progress, nps: tor.numPeers, len: tor.length, uspeed: tor.uploadSpeed };
            torrentsInfo.push(info);
        }
        if (torrentsInfo.length == 0) {
            return;
        }
        //
        let options = { client: 'web', rsId: this.state.rsId, torrents: torrentsInfo };
        //TODO calc avg / per hour speed here
        //  console.log("mining torrents: " + this.torrents);
        miner.reportSimple(options, ((ok, data) => {
            // console.log('report=' + ok + " data=" + data)
            if (ok && data) {
                this.setState({ totalPower: data.totalPower, bonus: data.bonus })
            } else {
                this.setState({ mining: false, msg: 'start ming err，please try again later.挖矿错误，稍后重试。' })
            }
        }).bind(this))
    }
    //
    handleReimburse() {
        miner.reimburse(1, '12345678123456781234567812345678', this.state.bonus, ((ok, data) => {
            if (ok) {
                this.setState({ msg: "OK" })
            } else {
                this.setState({ msg: "err" })
            }
        }).bind(this))
    }

    renderSubmitButon() {
        let active = true;
        if (this.state.bonus < 1)
            active = false;
        return (<Button disabled={!active} onClick={this.handleReimburse.bind(this)} color="primary">
            <i className="fa fa-lightbulb-o"></i>&nbsp;Submit(提现)
      </Button>)
    }

    renderStatus() {
        if (!this.state.mining)
            return null;
        else {
            return (<div>
                <Alert color="info">Mining working is going on...(正在挖矿中...) <span>&#x2198;{this.state.downloadSpeed}</span><span>&#x2197;{this.state.uploadSpeed}</span></Alert>
                <Alert color="info">Total network minging power(全网总算力){this.state.totalPower} GB.</Alert>
                <Alert color="info">Mine bous(挖矿收入){this.state.bonus} BTB.{this.renderSubmitButon()}</Alert>
                <PacmanLoader sizeUnit={"px"}
                    size={70} color={'#63c2de'} loading={this.state.mining} />
            </div>);
        }
    }

    renderMiningButton() {
        if (!this.state.mining) {
            return (<Button type="submit" color="primary" onClick={(e) => this.toggle()}>Start Mining(开始挖矿)</Button>)
        } else {
            return (<Button type="submit" color="primary" onClick={(e) => this.toggle()}>Stop Mining(停止挖矿)</Button>)
        }
    }

    renderSeret() {
        if (this.state.needSecret)
            return (<FormGroup>
                <Label htmlFor="appendedInputButtons">BTB Account Secret(BTB 账户密码):</Label>
                <div className="controls">
                    <InputGroup>
                        <Input id="appendedInputButtons" size="16" type="text" onChange={this.updateSecret.bind(this)} placeholder='type BTB account secret 输入BTB账户密码' />
                    </InputGroup>
                </div>
                <Label htmlFor="appendedInputButtons">BTB Account ID(BTB 账户ID):{this.state.rsId}</Label>
            </FormGroup>)
        else
            return (<Label htmlFor="appendedInputButtons">BTB Account ID(BTB 账户ID):{this.state.rsId}</Label>)
    }

    updateSecret(e) {
        if (e && e.target) {
            this.secret = e.target.value;
            let rsId = calcAccountId(this.secret);
            this.setState({ rsId })
            if (this.secret && this.secret.length > 30) {
                //miner.setSecret(this.secret);
            } else {
                this.setState({ needSecret: true })
            }
        }
    }

    renderMsg() {
        if (!this.state.msg)
            return null;
        return (<Alert color="danger">{this.state.msg}</Alert>)
    }

    render() {
        return (
            <div className="animated fadeIn">
                <Card>
                    <CardHeader>
                        <i className="icon-share">Web Miner to earn BTB coins(网页挖矿赚取BTB)</i>
                        <div className="card-header-actions">
                            <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                        </div>
                    </CardHeader>
                    <Collapse isOpen={true} id="collapseExample">
                        <CardBody>
                            {this.renderMsg()}
                            <FormGroup>
                                {this.renderSeret()}
                                <div className="controls">
                                    {this.renderMiningButton()}
                                </div>
                                {this.renderStatus()}
                            </FormGroup>
                            <div>
                                <Alert color="info">硬盘挖矿的的原理为利用本地硬盘存储P2P网络数据，共享数据给其他节点下载,同时获得BTB收益。</Alert>
                                <Alert color="danger">Please keep this page open.Mining work stopped as soon as page closed(挖矿过程中请不要关闭此网页，关闭网页挖矿自动停止)</Alert>
                            </div>
                        </CardBody>
                    </Collapse>
                </Card>
            </div>
        );
    }
}

export default WebMiner;



